import React, { Component } from 'react';
import '../../CSS/Toolbar.scss'
import 'bootstrap-icons/font/bootstrap-icons.css'
import '../../CSS/Icons.scss'
import { SaveAltSharp } from '@material-ui/icons';


class Toolbar extends Component {
    constructor(props) {
        super(props);
        this.state = { menuOpen: false, openedOnce: false  }
        this.handleIt = this.handleIt.bind(this);
    }

    handleIt(){
        var ele = document.getElementsByClassName("menu")[0];
        if(!this.state.menuOpen){
            ele.classList.add("menu-open");
        }
        else{
            ele.classList.remove("menu-open");
        }
        this.setState({menuOpen: !this.state.menuOpen});
        this.setState({openedOnce: true});
    }


    render() { 
        return (
        <div>
            <div className="download-schema">Hey Guys</div>
            <li className="toolbar-face-plate" onClick={this.handleIt}></li>
            <li className="toolbar">
                <i className = {`bi bi-arrow-down icon ${this.state.menuOpen ? 'rotate-up' : this.state.openedOnce ? 'rotate-down' : ''}`}></i>
                <ul className="menu">
                    <li className="menu-item menu-item-0">
                        <i className="bi bi-plus-square drop-down-icon"></i>
                    </li>
                    <li className="menu-item menu-item-1">
                        <i className="bi bi-info-square drop-down-icon"></i>
                    </li>
                    <li className="menu-item menu-item-2">
                        <i className="bi bi-x-square drop-down-icon"></i>
                    </li>
                </ul>
            </li>
        </div>
        );
    }
}
 
export default Toolbar;