import React, { Component } from 'react';
import MUIDataTable from "mui-datatables";

const columns =[
    {
    name: "name",
    label: "Name",
    options: {
        filter: true,
        sort: true,
    }
    },
    {
    name: "id",
    label: "Id",
    options: {
        filter: true,
        sort: true,
    }
    },
    {
    name: "type",
    label: "Type",
    options: {
        filter: true,
        sort: true,
    }
    },
];

const data = [
    {
        name: "Mock_data_0",
        id: "0",
        type: "CAD"
    },
    {
        name: "Mock_data_1",
        id: "1",
        type: "CAD"
    },
    {
        name: "Mock_data_2",
        id: "2",
        type: "CAD"
    },
    {
        name: "Mock_data_3",
        id: "3",
        type: "CAD"
    },
    {
        name: "Mock_data_4",
        id: "4",
        type: "CAD"
    },
    {
        name: "Mock_data_5",
        id: "5",
        type: "CAD"
    },
];

const options = {
    tableBodyheight: "300px",
    tableBodyWidth: "300px",
    rowsPerPageOptions: [10, 15]
};

class MatTable extends Component {
    constructor(props) {
        super(props);
        this.state = {  }
    }

    render() { 
        return ( 
        <div>
            <MUIDataTable
            title={"Mock Data Table"}
            data ={data}
            columns ={columns}
            options= {options}>
            </MUIDataTable>
        </div>
    );
    }
}
 
export default MatTable;