import React, { Component } from 'react';
import '../../CSS/Dialogue.scss';
import '../../CSS/Icons.scss';
import '../../CSS/BackCover.scss';
import '../../CSS/MatTable.scss';
import 'bootstrap-icons/font/bootstrap-icons.css';
import'../Table/MatTable.js';
import MatTable from '../Table/MatTable.js';



class MainDialogue extends Component {
    constructor(props) {
        super(props);
        this.state = { openedOnce: false, opened: false }
        this.openDialogue = this.openDialogue.bind(this);
    }

    openDialogue(){
        this.setState({openedOnce: true});
        this.setState({opened: !this.state.opened});
    }

    render() { 
        return (
            <div>
                <div className={`dialogue ${this.state.opened ? 'dialogue-slide-out' : this.state.openedOnce ? 'dialogue-slide-in' : ''}` }>
                    <i onClick={this.openDialogue} className={`bi icon-lg float-left float-right ${this.state.opened ? 'bi-arrow-bar-left' : 'bi-arrow-bar-right'}`}></i>
                    { this.state.opened ?
                        <div className="table-adjuster">
                            <MatTable></MatTable>
                        </div> : null
                    }
                </div>
                {this.state.opened ? <div className="backcover"></div> : null}
            </div>
          );
    }
}
 
export default MainDialogue;