import React, { Component, useState } from 'react';
import ReactFlow , {Background, isEdge, Controls} from 'react-flow-renderer';
import MockData from '../../MockData/MockData.js'
import MainDialogue from '../Dialogues/MainDialogue.js';
import cytoscape from 'cytoscape';
import dagre from 'cytoscape-dagre';


class FlowChart extends Component {

    constructor(props) {
        super(props);
        cytoscape.use(dagre);
        this.state = { data: MockData,
             flowData: [],
             viewHeight: 1,
             viewWidth: 1,
             selectedNode: null
            };

        this.selectElement = this.selectElement.bind(this);
        this.deselectElement = this.deselectElement.bind(this);
    }

    componentDidMount(){
        this.initFlowData();
        this.createCytoScape();
        this.setState({viewWidth: window.innerWidth});
        this.setState({viewHeight: window.innerHeight});
    }


    createNodesAndEdges = () => {
        var data = [];
        data.push({
            data: {
            id: "root",
            weight: 100,
            }
        })
        for(var i = 0; i < 10; i++){
            data.push({
                data: {
                id: "node_" + i,
                weight: 50,
                }
            });
            data.push({
                data: {
                id: "edge_" + i,
                source:"root",
                target:"node_" + i
                }
            });

            for(var j = 0; j < 10; j++){
                data.push({
                    data:{
                        id: "nodeChild_" + j + i,
                        weight: 25
                    }
                });
                data.push({
                    data:{
                    id: "childEdge_" + j + i,
                    source: "node_" + i,
                    target: "nodeChild_" + j + i
                    }
                });
            }
        }
        return data;
    }

    createEdges = () => {
        var data = [];
        for(var i = 0; i < 10; i++){
            data.push({
                id: "edge_" + i,
                source:"root",
                target:"node_" + i
            });
            for(var j = 0; j < 10; j++){
                data.push({
                    id: "childEdge_" + j + i,
                    source: "node_" + i,
                    target: "nodeChild_" + j + i 
                });
            }
        }
        return data;
    }

    createNodes = () => {
        var data = [];
        data.push({
            data: {
            id: "root"
            }
        })
        for(var i = 0; i < 10; i++){
            data.push({
                data: {
                id: "node_" + i
                }
            });
            for(var j = 0; j < 10; j++){
                data.push({
                    data:{
                        id: "nodeChild_" + j
                    }
                });
            }
        }
        return data;
    }

    createCytoScape = () => {
        var data = this.createNodesAndEdges();
        var cy = cytoscape({
            elements: data,
        });
        cy.layout({name:'concentric', condense:true, avoidOverlapPadding:200, minNodeSpacing: 200, cols: 30, nodeSep: 200, rankSep: 100, fit: true, boundingBox: {x1: 0, y1: 100, w: 10000, h: 10000}}).run();
        return cy;
    }

    getRandomNum = (min, max) => {
        min = Math.ceil(min);
        max = Math.floor(max);
        return Math.floor(Math.random() * (max - min + 1) + min);
    }

    initFlowData = () => {
        var flowData = [];
        var cy = this.createCytoScape();
        var nodes = cy.nodes();
        flowData = flowData.concat(this.createEdges());
        nodes.forEach(element => {
            flowData.push({
                id: element._private.data.id,
                data: {label: element._private.data.id},
                position: {x: element._private.position.x, y: element._private.position.y}
            });
        });
        this.setState({flowData: flowData})
    }

    selectElement(event, element){
        if(!isEdge(element)){
            this.setState({selectedNode: element});
            console.log("Element Selected");
        }
    }

    deselectElement(event){
        this.setState({selectedNode: null});
        console.log("Element Deselected");
    }




    render() { 
        return (<div style={{ height: this.state.viewHeight, width: this.state.viewWidth }}>
            <ReactFlow
            elements={this.state.flowData}
            onElementClick={this.selectElement}
            onPaneClick={this.deselectElement}
            >
            <Background color="#aaa" gap={16} ></Background>
            <Controls></Controls>
            </ReactFlow>
            {
                (this.state.selectedNode != null) ? <MainDialogue selectedNode={this.state.selectedNode} data={this.state.data}></MainDialogue> : null
            }
        </div>  );
    }
}
 
export default FlowChart;